form {
  background-color: rgb(255, 71, 71);
  /* margin: 8rem auto; */
  width: 50%;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  padding: 10rem;
}

.form__input {
  padding: 1rem;
}

input {
  float: right;
  position: relative;
  height: 3rem;
  border-radius: 0.5rem;
  width: 100%;
}
textarea {
  width: 100%;
  height: 15rem;
  border-radius: 0.5rem;
  resize: none;
}
label {
  color: white;
  font-size: 1.5rem;
}
